import React from 'react';
import {store} from '../stores/index';
import axios from 'axios';
import TopArtistCountry from '../data/TopArtistCountryBase';
import TopArtistCountryView from '../components/TopArtistCountry';
import TopSongCountry from '../data/TopSongCountryBase';
import TopSongCountryView from '../components/TopSongCountry';
import RedirectPage from '../actions/redirectPageAction';
import ResultsList from '../data/result';
import ResultsView from '../components/result';
const API_KEY = "f5352f03bdb1008ab36c91faeec4bc6a";

class Home extends React.Component{
	constructor(){
		super();
		this.state={
			toggleSearch:false,
			topArtists:TopArtistCountry,
			topSongs:TopSongCountry,
			country:"",
			switch:"SONGS->",
			category:"artists",
			keyword:"a",
			results:ResultsList
		}
		this.switchTo = this.switchTo.bind(this);
		this.handleChange = this.handleChange.bind(this);
		this.searchSong = this.searchSong.bind(this);
	}
	handleChange(e){
		const {name,value} = e.target;
		this.setState({
			...this.state,
			[name]:value
		});
	}
	searchSong(){
		ResultsList.splice(0,ResultsList.length);
		const keyword = this.state.keyword;
		if(keyword.trim()!==""){
			axios
			.get(`http://ws.audioscrobbler.com/2.0/?method=track.search&track=${keyword}&api_key=${API_KEY}&limit=32&format=json`)
			.then(response=>{
				const tracks=response.data.results.trackmatches.track;
				console.log(tracks);
				for(let i = 0 ;i<tracks.length;++i){
					ResultsList.push({name:tracks[i].name,artist:tracks[i].artist,url:tracks[i].url,img:tracks[i].image[3]['#text'],listeners:tracks[i].listeners});
				}
				this.setState({
					...this.state,
					results:ResultsList
				})
			});
		}else{
			alert("Không nhập gì sao kiếm cho mày!?");
		}
	}
	switchTo(e){
		let bg = document.getElementById('bg-home');
		let songField = document.getElementById('top-song-base-country');
		let artistField = document.getElementById('top-artist-base-country');
		if(this.state.switch==="SONGS->"){
			songField.style.left="2%";
			songField.style.filter="brightness(100%)";
			songField.style.boxShadow="0px 0px 52px 2px rgba(0,0,0,0.75)";
			songField.style.top="10%";
			artistField.style.filter="brightness(20%)";
			artistField.style.boxShadow="none";
			artistField.style.left="-100%";
			artistField.style.top="13%";
			bg.style.backgroundColor="black";
			setTimeout(()=>{
				this.setState({
					...this.state,
					category:"songs",
					switch:"<-ARTISTS"
				})
			},2000);
		}else if(this.state.switch==="<-ARTISTS"){
			songField.style.left="100%";
			songField.style.filter="brightness(50%)";
			songField.style.boxShadow="none";
			songField.style.top="13%";
			artistField.style.filter="brightness(100%)";
			artistField.style.boxShadow="0px 0px 52px 2px rgba(0,0,0,0.75)";
			artistField.style.left="0%";
			artistField.style.top="10%";
			bg.style.backgroundColor="white";
			setTimeout(()=>{
				this.setState({
					...this.state,
					category:"artists",
					switch:"SONGS->"
				})
			},2000);
		}
			
	}
	componentDidMount(){
		TopArtistCountry.splice(0,TopArtistCountry.length);
		TopSongCountry.splice(0,TopSongCountry.length);
		axios
		.get(`https://ws.audioscrobbler.com/2.0/?method=geo.gettopartists&country=${store.getState().country}&api_key=${API_KEY}&limit=12&format=json`)
		.then(response=>{
			const artist=response.data.topartists.artist;
			const country = response.data.topartists['@attr'].country;
			for(let i=0;i<artist.length;++i){
				TopArtistCountry.push({name:artist[i].name,url:artist[i].url,
					img:artist[i].image[4]['#text'],listeners:artist[i].listeners});
				
			}
			this.setState({
					...this.state,
					topArtists:TopArtistCountry,
					country:country
				});
		})
		axios
		.get(`https://ws.audioscrobbler.com/2.0/?method=geo.gettoptracks&country=${store.getState().country}&api_key=${API_KEY}&limit=12&format=json`)
		.then(response=>{
			const songs = response.data.tracks.track;
			for(let i = 0;i<songs.length;++i){
				TopSongCountry.push({name:songs[i].name,artist:songs[i].artist.name,
					artistUrl:songs[i].artist.url,url:songs[i].url,img:songs[i].image[3]['#text'],
					listeners:songs[i].listeners});
			}
			this.setState({
				...this.state,
				topSongs:TopSongCountry
			});
		})
		ResultsList.splice(0,ResultsList.length);
		const keyword = this.state.keyword;
		axios
		.get(`http://ws.audioscrobbler.com/2.0/?method=track.search&track=${keyword}&api_key=${API_KEY}&limit=32&format=json`)
		.then(response=>{
			const tracks=response.data.results.trackmatches.track;
			console.log(tracks);
			for(let i = 0 ;i<tracks.length;++i){
				ResultsList.push({name:tracks[i].name,artist:tracks[i].artist,url:tracks[i].url,img:tracks[i].image[3]['#text'],listeners:tracks[i].listeners});
			}
			this.setState({
				...this.state,
				results:ResultsList,
				keyword:""
			})
		});

	}
	render(){
		const artistView = this.state.topArtists.map(artist=><TopArtistCountryView key={artist.name} artist={artist}/>);
		const songView = this.state.topSongs.map(song=><TopSongCountryView key={song.name} song={song}/>);
		const resultsView = this.state.results.map(song=><ResultsView key={song.name} song={song}/>)
	return(
		<section>
			<div id="bg-home">
				<div id="home-main-container">
					<h1>Hi {store.getState().username}. These are some popular 
					<span style={{'box-shadow': '10px 10px 5px 0px rgba(0,0,0,0.75)',
					'color':'#df2029','padding':'10px','backgroundColor':'white'}}>
					{this.state.category}</span> in <span style={{'textDecoration':'overline',
					'color':'#df2029','backgroundColor':'white','padding':'10px',
					'box-shadow': '10px 10px 5px 0px rgba(0,0,0,0.75)'}}>{this.state.country}
					</span></h1>
					<div id="top-artist-base-country">
						{artistView}
					</div>
					<div id="top-song-base-country">
						{songView}
					</div>
					<button onClick={this.switchTo} id="swich-to">{this.state.switch}</button>
				</div>
					<div id="red-gif">
				</div>
					<h1 id="or">Or</h1>
					<div id="search-bar-holder">
						<input type="text" value={this.state.keyword} onChange={this.handleChange} name="keyword" placeholder="SEARCH SONG"/>
 						<button onClick={this.searchSong}>SEARCH</button>
 					</div>
 					<div id="song-results">
 						{resultsView}
 					</div>
				</div>
		</section>
		)	
	}
}
export default Home;